import { Component, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import { TRadioOptions } from './type';
import { joinClass } from '../../../utils/joinClass';

@Component({
  tag: 'charma-radio-group',
  styleUrl: 'charma-radio-group.scss',
  shadow: true,
})
export class CharmaRadioGroup {
  // Declare props
  @Prop() label = '';
  @Prop() name = 'charma-radio-group';
  @Prop() options: TRadioOptions = [];
  @Prop({ reflect: true }) selected: string = '';
  @Prop() required = true;
  @Prop() error = false;
  @Prop() orientation: 'vertical' | 'horizontal' = 'vertical';

  @Event() valueChange: EventEmitter<string>;

  handleChange = (event: Event) => {
    this.valueChange.emit((event.target as HTMLInputElement).value);
  };

  render() {
    return (
      <Host>
        <slot>
          <div class={joinClass('flex ', this.orientation === 'vertical' ? 'flex-row gap-5' : ' flex-col')}>
            {this.options.map(({ title, value }) => {
              return (
                <div class="flex items-center mb-4 ">
                  <input
                    onChange={this.handleChange}
                    id={`rb-${title}`}
                    type="radio"
                    name={`rb-${title}`}
                    value={value}
                    class="h-4 w-4 border-gray-300 focus:ring-2 focus:ring-blue-300 cursor-pointer font-body text-base transition-transform"
                    checked={this.selected === value}
                  />
                  <label htmlFor={`rb-${title}`} class="font-body text-base text-gray-900 ml-2 block capitalize cursor-pointer">
                    {title}
                  </label>
                </div>
              );
            })}
          </div>
        </slot>
      </Host>
    );
  }
}
